export const getRisingStarTable = (info, lng) => {
  const {
    month,
    risingStarBottom,
    bottomLeft1,
    bottomRight1,
    bottom1,
    risingStarTop,
    risingStarLeft,
    risingStarRight,
    topLeft1,
    topRight1,
    year,
    day,
    manCenter,
    womanCenter,
    groundCenter,
    honorAndGloryCenter,
    risingStarSky,
    risingStarEarth,
  } = info;

  const data = [
    {
      karmaName: lng === 'ua' ? ' ' : ' ',
      info: [
        {
          type: lng === 'ua' ? ' ' : ' ',
          energyInfo: [
            {
              name: lng === 'ua' ? ' ' : ' ',
              energy: month,
            },
            {
              name: lng === 'ua' ? '  ' : '  ',
              energy: risingStarBottom,
            },
            {
              name: lng === 'ua' ? ' ' : ' ',
              energy: `${bottomLeft1} - ${bottomRight1} - ${risingStarBottom}`,
            },
            {
              name: '',
              energy: manCenter,
            },
          ],
        },
        {
          type: lng === 'ua' ? ' ' : ' ',
          energyInfo: [
            {
              name: '',
              energy: bottom1,
            },
            {
              name: lng === 'ua' ? '  ' : '  ',
              energy: risingStarTop,
            },
            {
              name: lng === 'ua' ? ' ' : ' ',
              energy: `${topLeft1} - ${topRight1} - ${risingStarTop}`,
            },
            {
              name: '',
              energy: womanCenter,
            },
          ],
        },
      ],
      total: {
        name: lng === 'ua' ? '  ' : '  ',
        keys: [manCenter, womanCenter, risingStarSky],
      },
    },
    {
      karmaName: lng === 'ua' ? ' ' : ' ',
      info: [
        {
          type: lng === 'ua' ? '  ' : '  ',
          energyInfo: [
            {
              name: lng === 'ua' ? " '" : ' ',
              energy: year,
            },
            {
              name: lng === 'ua' ? ' ' : ' ',
              energy: risingStarLeft,
            },
            {
              name:
                lng === 'ua'
                  ? '  '
                  : '  ',
              energy: `${topLeft1} - ${bottomLeft1} - ${risingStarLeft}`,
            },
            {
              name:
                lng === 'ua'
                  ? '    '
                  : '   ',
              energy: groundCenter,
            },
          ],
        },
        {
          type: lng === 'ua' ? '   ' : '   ',
          energyInfo: [
            {
              name: lng === 'ua' ? ' , ' : ' , ',
              energy: day,
            },
            {
              name: '',
              energy: risingStarRight,
            },
            {
              name: lng === 'ua' ? '   ' : '   ',
              energy: `${topRight1} - ${bottomRight1} - ${risingStarRight}`,
            },
            {
              name: lng === 'ua' ? '   ' : '   ',
              energy: honorAndGloryCenter,
            },
          ],
        },
      ],
      total: {
        name: lng === 'ua' ? '  ' : '  ',
        keys: [groundCenter, honorAndGloryCenter, risingStarEarth],
      },
    },
  ];
  return data;
};
